import React, { useState } from 'react';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import TopPannel from './TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import { isMobileOnly } from 'react-device-detect';
import Modal from 'react-bootstrap/Modal';
import { MdCall } from 'react-icons/md';
import { FaWhatsapp } from 'react-icons/fa';
import { AiTwotoneMail } from 'react-icons/ai';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { baseImgURL } from 'assets/Constant';

export default function MenuScreen(props) {
  const [clientData, setClientData] = useState({});
  const [loading, setLoading] = useState(true);
  const [showSupportModal, setShowSupportModal] = React.useState(false);
  React.useEffect(() => {
    setLoading(true)
    try {
      db.get(db.model.user).then(async (user) => {
        await getClientData(user.userData.id)
        setLoading(false)
      })
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  }, [])

  const getClientData = async (id) => {
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          setClientData(aa)
          await db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
        setClientData(p)
      }
    })
  }

  const onLogout = async () => {
    await db.clear();
    props.history.push("/signup")
  };

  const SupportModal = () => {
    return (
      <Modal
        show={showSupportModal}
        onHide={() => {setShowSupportModal(false)}}
				keyboard={false}
        centered
        style={{padding:'8%'}}
      >
        <Modal.Body>
          <div className='header2dark'>Need help?</div>
          <div className='smallTextDark' style={{ marginTop: '4%', marginBottom: 0 }}>
            Drop us an email on support@mintbox.ai or call/WhatsApp us on 9167315915 with your query
          </div>
          <div style={{textAlign:'right',marginTop:'24px'}}>
            <a href="tel://9167315915"><MdCall size={24} color={cssVar.getPropertyValue('--indianEquity')} style={{marginLeft:'16px'}}/></a>
            <a href='https://wa.me/919167315915'><FaWhatsapp size={24} color='#25D366' style={{marginLeft:'16px'}}/></a>
            <a href="mailto:support@mintbox.ai"><AiTwotoneMail size={24} color='EF7479' style={{marginLeft:'16px'}}/></a>
          </div>
        </Modal.Body>
      </Modal>
    )
  }
  if(loading){
    return <ActivityLoaderMintbox/>
  }
  return (
    <div className='mainContainer'>
      <MainHeader 
        header='Menu' 
        right='What are mintboxes?'
        rightAction={() => props.history.push({ pathname: '/video-player', state: { id: 1 }})} 
        navigation={props.history} />
      <div className='dataContainer px-0'>
        <TopPannel />
        <div className='menu-card' onClick={() => props.history.push('/menu/profile')}>
          <img src={baseImgURL+"menu_user.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Profile</div>
            <div className='smallTextLight'>Your risk level, demat and mutual fund accounts</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => props.history.push('/menu/notifications')}>
          <img src={baseImgURL+"menu_notification.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Notifications</div>
            <div className='smallTextLight'>Updates and actions to keep your account and portfolio on track</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => props.history.push('/menu/reports')}>
          <img src={baseImgURL+"menu_report.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Reports</div>
            <div className='smallTextLight'>Portfolio, transactions, capital gains, ELSS reports</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => props.history.push('/menu/mf-transaction-pin')}>
          <img src={baseImgURL+"menu_pin.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Mutual fund transaction PIN</div>
            <div className='smallTextLight'>Edit/remove PIN for mutual fund transactions. This PIN helps you execute your MF transactions on mintbox</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => setShowSupportModal(true)}>
          <img src={baseImgURL+"menu_support.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Support</div>
            <div className='smallTextLight'>Need help? Drop us an email on support@mintbox.ai or call/WhatsApp us on 9167315915 with your query</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => {
          // if (isAndroid) {
          //   window.location.href =
          //     "https://play.app.goo.gl/?link=https://play.google.com/store/apps/details?id=com.finpeg.mutualfunds";
          // } else if (isIOS) {
          //   window.location.href =
          //     "https://apps.apple.com/in/app/finpeg-mutual-funds/id1471498985";
          // }
          if(isMobileOnly){
            const win = window.open("/qr-custom/app-link", "_blank");
  win.focus();
          }
        }}>
          <img src={baseImgURL+"menu_rate.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark'>Rate us</div>
            <div className='smallTextLight'>Rate us on Play Store/App Store or write your feedback to hello@mintbox.ai</div>
          </div>
        </div>
        <div className='menu-card' onClick={() => onLogout()} style={{ marginBottom: '4%' }}>
          <img src={baseImgURL+"menu_logout.webp"} className='menu-card-icon' />
          <div className='menu-card-body'>
            <div className='bodyTextDark' style={{ color: cssVar.getPropertyValue('--red') }}>Log out</div>
          </div>
        </div>
      </div>
      <SupportModal />
    
    </div>
  )
}
